.home {

    &::-webkit-scrollbar {
        display: none;
    }

    &__slide {
        position: relative;
        width: 100%;
        height: 0;
        padding-bottom: 40%;
    }

    &__slideWrap {
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
    }


    &__content {
        padding: 0 20px 49px;
        list-style: none;
        margin: 0;

        li {
            list-style: none;

            &:nth-child(n + 2) {
                border-top: 1px solid #E4E2E2;
            }
        }
    }
}